export default {
    props: {
        name: {
            type: String,
            required: true,
            default: () => 'undefined'
        },
        description: {
            type: String,
            required: true,
            default: () => 'undefined'
        },
        id: {
            type: Number,
            required: false,
            default: () => 1
        },
    },
    methods: {
        remove(){
            this.$parent.removeIngredient(this.id)
        },
        update() {
            this.$router.push('EditIngredient')
        }
    }
}
